@import './mixins';
@import './variables';

body,
html {
    padding: 0;
    margin: 0;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    margin-bottom: 48px;
    overflow: hidden;
}

.header {
    width: 100%;
    // height: ;
    max-height: 20%;
    box-shadow: 0px 5px 5px black;
    background:rgba(2, 0, 0, 0.836);
    transition: background 0.5s ease;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 1.2rem;
    color: white;
    padding: 1em;
    font-size: 3rem;
    .make-bigger {
        scale: 2;
        padding-right: 12px;
        font-weight: 600;   
        margin-left: 50px;
    }
}

.footer {
    width: 100%;
    background:rgba(2, 0, 0, 0.9);
    transition: background 0.5s ease;
    font-size: 1rem;
    line-height: 1;
    // margin-top: auto;
    @include flex-center;
    overflow: hidden;
    // position: absolute;
    // bottom: 0px;
    color: $white;
    height: 5vh;
}

.project,
.scheduled-item {
    display: flex;
    flex-direction: row;
    margin: 0.75rem;
    padding: 0.1rem;
    padding-left: 1rem;
    border-radius: 12px;
    align-items: center;
    p {
        font-size: large;
        padding-left: 10px;
    }
    &:hover {
        cursor: pointer;
        background-color: adjust-hue($color: $light-grey, $degrees: 200);
    }
}

.sidebar {
    background-color: $light-grey;
    width: 350px;
    padding: 5px;
    z-index: 5;
    height: 79vh;
}

.active {
    background-color: adjust-hue($color: $light-grey, $degrees: 10);
    font-weight: 600;
}

.project-container {
    .project-heading {
        margin-bottom: 5px;
        margin-left: 25px;
        font-weight: 600;
        font-size: 1.75rem;
    }
}